<script>
import axios from "axios";
import { ref, computed } from "vue";

export default {
  name: "SelectPlan",
  setup() {
    const plan = ref("");
    const loading = ref(false);
    const message = ref("");
    const success = ref(false);
    const messageClass = computed(() =>
      success.value ? "alert-success" : "alert-danger"
    );

    async function subscribe(selectedPlan) {
      loading.value = true;
      message.value = "";
      try {
        const r = await axios.post("/api/users/subscribe", {
          plan: selectedPlan,
        });
        success.value = true;
        message.value = r.data.message || "تم تفعيل الخطة بنجاح ✅";
      } catch (err) {
        success.value = false;
        message.value =
          err.response?.data?.error || "حدث خطأ أثناء تفعيل الخطة";
      } finally {
        loading.value = false;
      }
    }

    return {plan, subscribe, loading, message, messageClass };
  },
};
</script>